body{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}
.header{
    position: fixed;
    inset: 0 0 0;
    height: 40px;
    background-color: rgba(150, 150, 150, 0.8);
    
    z-index: 100;
}
.slider{
    height: 100vh;
    width: 100%;
    background-color: aqua;
}





.containerSliderPrincipal{
    -webkit-user-select: none;
            user-select: none;
    /* box-shadow: 0px 1px 5px 3px rgba(79, 79, 79, 0.5);
    border-radius: 20px; */
    width: 100%;
    height: 50vw;
    max-height: 100vh;
    overflow: hidden;
    margin: auto;
    position: relative;
}
.imgSliderPrincipal{
    pointer-events: none;
    width: 100%;
    height: 50vw;
    max-height: 100vh;
    object-fit: cover;
}
.sliderPrincipal{
    background-color: #e7e7e7;
    height: 50vw;
    max-height: 100vh;
}
.sliderSection{
    display: none;
}
.carrouselActive{
    display: block;
    visibility: visible;
    -webkit-animation: imgScale linear .4s forwards;
            animation: imgScale linear .4s forwards;
}
@-webkit-keyframes imgScale {
    0%{opacity: .5;}
    
    100%{opacity: 1;}
}
@keyframes imgScale {
    0%{opacity: .5;}
    
    100%{opacity: 1;}
}

.containerBtn{
    position: absolute;
    top: 0;
    cursor: pointer;
    place-items: center;
    display: grid;
    height: 100%;
    width: 30%;
}
.containerBtn p{
    -webkit-user-select: none;
            user-select: none;
    Color: rgba(255, 255, 255, 0.7);
    width: 40px;
    font-size: 60px;
    font-family: monospace;
}
.containerBtn:hover p{
    Color: rgba(255, 255, 255);
}
.sliderbtnLeft{
    left: 0;
}
.sliderbtnRight{
    right: 0;
}
.sliderbtnLeft p{
    position: absolute;
    left: 10px;
}
.sliderbtnRight p{
    position: absolute;
    right: 10px;
    
}
.nivelContainer{
    background-color: rgb(135, 135, 135);
    width: 97%;
    margin: 15px auto;
    border: solid 3px black;
    border-radius: 20px;
    overflow: hidden;
    text-align: center;
}
.nivelContainer .info{
    width: 85%;
    margin: 10px auto;
    border-radius: 20px;
    display: flex;
}
.info img{
    border: solid 1px black;
    object-fit: cover;
    width: 30%;
    height: 300px;
    border-radius: 20px;
}
.info p{
    font-size: 22px;
}
